<template>
    <section>
        Toys Statistics:
        <toy-charts :data="dataChart"/>
    </section>
</template>

<script>
import toyCharts from "@/components/toy-charts";
export default {
  data() {
    return {
    };
  },
  components: {
    toyCharts
  },
  computed: {
      typesCounts() {
          return this.$store.getters.typesCount
      },

      types() {
          return this.typesCounts.map(type => type._id)
      },

      counts() {
          return this.typesCounts.map(type => type.count)
      },

      dataChart() {
          return {
            dataset: [{
                backgroundColor: ['red', 'green', 'blue', 'yellow', 'orange', 'purple', 'grey'],
                data: this.counts
                }],
            type: this.types
          }
      },
  },
  created() {
      this.$store.dispatch({type: 'getTypesCount'})
  }
};
</script>

