<template>
    <section v-if="toy">
        <div>
            Toy name: {{toy.name}}
        </div>
        <div>
            Toy price: {{toy.price}}
        </div>
        <div>
            Toy Type: {{toy.type}}
        </div>
        <div>
            In Stock? {{toy.inStock? 'Yes' : 'No'}}
        </div>
        <div>
            Added to store at: {{toy.createdAt}}
        </div>
        <router-link to="/">Back to Toys list</router-link>
    </section>
</template>

<script>
export default {
  name: "toy-details",
  data() {
    return {
        toy: null,
    };
  },

  components: {},

  computed: {},

  methods: {},

  created() {
    const toyId = this.$route.params.toyId;
    if (toyId) {
      this.$store.dispatch({ type: "getById", toyId }).then(toy => {
        this.toy = toy;
      });
    }
  }
};
</script>
