<template>
    <section>
        <form>
            <label>
                <p>Filter by name:</p>
                <el-input
                    placeholder="Type something"
                    prefix-icon="el-icon-search"
                    v-model="filter.byName"
                    @input="setFilter">
                </el-input>
                <!-- <input type="text" v-model="filter.byName" @input="setFilter"> -->
            </label>
            <label>
                <p>Filter by existence in stock:</p>
                <el-select v-model="filter.byStatus" @change="setFilter" placeholder="Select">
                    <el-option label="All" value="all"></el-option>
                    <el-option label="In Stock" value="inStock"></el-option>
                    <el-option label="Not In Stock" value="notInStock"></el-option>
                </el-select>
                <!-- <select v-model="filter.byStatus" @change="setFilter">
                    <option value="all">All</option>
                    <option value="inStock">In Stock</option>
                    <option value="notInStock">Not in Stock</option>
                </select> -->
            </label>
            <label>
                <p>Filter by type:</p>
                <select v-model="filter.byType" @change="setFilter">
                    <option value="all">All</option>
                    <option v-for="(type, idx) in types" :key="idx" :value="type">{{type}}</option>
                </select>
            </label>
            <label>
                <el-select v-model="filter.sort" placeholder="Sort" @change="setFilter">
                    <el-option-group label="By Name">
                        <el-option
                            label="A-Z"
                            value="name_1">
                        </el-option>
                        <el-option
                            label="Z-A"
                            value="name_-1">
                        </el-option>
                    </el-option-group>
                    <el-option-group label="By Price">
                        <el-option
                            label="Low First"
                            value="price_1">
                        </el-option>
                        <el-option
                            label="High First"
                            value="price_-1">
                        </el-option>
                    </el-option-group>
                </el-select>
                <!-- <p>Sort by:</p> -->
                <!-- <select v-model="filter.sort" @change="setFilter">
                    <option value="name">Name</option>
                    <option value="price">Price</option>
                </select> -->
            </label>
        </form>
    </section>
</template>

<script>
export default {
  data() {
    return {
      filter: {
        byName: "",
        byStatus: "all",
        byType: "all",
        sort: "name_1"
      }
    };
  },

  components: {},

  computed: {
    types() {
      return this.$store.getters.types;
    }
  },

  methods: {
    setFilter() {
      this.$store.dispatch({ type: "setFilter", filter: this.filter });
    }
  },

  created() {}
};
</script>
