<template>
    <section>
        <h2>{{toy._id? 'Edit' : 'Add'}} Toy</h2>
        <form @submit.prevent="saveToy">
            <label>
                <p>Name:</p>
                <input v-validate="'required|alpha'" name="name" type="text" v-model="toy.name" placeholder="Enter Toy Name">
                <span>{{ errors.first('name') }}</span>
            </label>
            <label>
                <p>Price:</p>
                <input type="number" v-validate="'required|numeric'" name="price" min="0" v-model="toy.price">
                <span>{{ errors.first('price') }}</span>
            </label>
            <label>
                <p>Type:</p>
                <input type="text" v-model="toy.type" placeholder="Enter Toy Type">
            </label>
            <label>
                <input type="checkbox" :checked="toy.inStock" v-model="toy.inStock">
                <p>{{toy.inStock? 'In' : 'Not in' }} stock</p>
            </label>
            <button>{{toy._id? 'Update' : 'Add'}} toy</button>
        </form>
        <router-link to="/">Cancel</router-link>
    </section>
</template>

<script>
export default {
  name: "toy-edit",
  data() {
    return {
      toy: {
        name: "",
        price: 0,
        type: "",
        inStock: true
      }
    };
  },

  components: {},

  computed: {},

  methods: {
    saveToy() {
      this.$store.dispatch({ type: "saveToy", toy: this.toy })
        .then(_ => {
            console.log('Saved Toy Succefully');
            this.$router.push('/')
        });
    }
  },

  created() {
    const toyId = this.$route.params.toyId;
    if (toyId) {
      this.$store.dispatch({ type: "getById", toyId })
        .then(toy => {
            this.toy = toy;
        });
    }
  }
};
</script>
